import { basicSetup } from 'codemirror';
import { EditorView, keymap } from '@codemirror/view';
import { EditorState } from '@codemirror/state';
import { search, searchKeymap } from '@codemirror/search';
import { bracketMatching, indentOnInput } from '@codemirror/language';
import { defaultKeymap, historyKeymap } from '@codemirror/commands';
import { lintGutter } from '@codemirror/lint';

export function initEditor(parent: HTMLDivElement, onSave: (view: EditorView) => boolean) {
  const container = document.createElement('div');
  container.className = 'editor-container';
  container.style.position = 'absolute';
  container.style.left = '0';
  container.style.right = '0';
  container.style.overflow = 'hidden';
  container.style.pointerEvents = 'auto';
  parent.appendChild(container);

  const extensions = [
    basicSetup,
    indentOnInput(),
    bracketMatching(),
    search({ caseSensitive: false, regexp: false }),
    lintGutter(),
    keymap.of([
      {
        key: 'Mod-s',
        preventDefault: true,
        run: onSave,
      },
      ...historyKeymap,
      ...searchKeymap,
      ...defaultKeymap,
    ]),
    EditorView.theme({
      '&': {
        height: '100%',
      },
      '.cm-scroller': {
        overflow: 'auto',
        height: '100%',
      },
    }),
  ];

  const view = new EditorView({
    state: EditorState.create({
      doc: 'this is the source code for the synth',
      extensions,
    }),
    parent: container,
  });


  function setContents(contents: string) {
    view.setState(EditorState.create({ doc: contents, extensions }));
  }

  return { view, container, setContents };
}
