import { P_GAIN, P_GLIDE, P_PARAM_A, P_PARAM_B, P_PARAM_C, P_PARAM_D, P_PRESSURE, P_SLIDE, P_TEMPO, SAMPLE_RATE, TAU } from './constants';
import { clamp, SpeedTracker, toFrequency } from './helpers';
