class Oscilloscope extends Thing {
  constructor(options: ThingOptions, readonly synth: Synth) {
    super({ hasBackground: false, isActive: () => activeSynth === this.synth, ...options });
  }

  pointerDownY: number | null = null;

  onPointerDown(x: number, y: number) {
    this.synth.makeActive();
    this.pointerDownY = y;
    return this;
  }

  onPointerMove(x: number, y: number) {
    const dy = y - this.pointerDownY!;
    if (editingPatch) {
      padRowOffset =
        -ui.EDITOR_HEIGHT_IN_PADS + (dy > 0 ? Math.min(dy / padHeight, ui.EDITOR_HEIGHT_IN_PADS) : 0);
    } else {
      padRowOffset = dy < 0 ? Math.max(dy / padHeight, -ui.EDITOR_HEIGHT_IN_PADS) : 0;
    }
  }

  onPointerUp(x: number, y: number) {
    if (editingPatch) {
      if (padRowOffset > 1 - ui.EDITOR_HEIGHT_IN_PADS) {
        editingPatch = false;
        padRowOffset = 0;
      } else {
        padRowOffset = -ui.EDITOR_HEIGHT_IN_PADS;
      }
    } else {
      if (padRowOffset <= -1) {
        editingPatch = true;
        padRowOffset = -ui.EDITOR_HEIGHT_IN_PADS;
      } else {
        padRowOffset = 0;
      }
    }
    this.pointerDownY = null;
  }

  render() {
    const r = ui.RECTANGLE_RADIUS;
    const inset = 3;
    const sx = this.x + inset;
    const sy = this.y + inset;
    const sw = this.w - inset * 2;
    const sh = this.h - inset * 2;

    // Outer bezel (CRT housing)
    ctx.beginPath();
    ctx.fillStyle = ui.SCOPE_BEZEL;
    ctx.roundRect(this.x, this.y, this.w, this.h, r + 1);
    ctx.fill();

    // Screen: dark CRT glass with subtle center lift (curvature)
    const screenGrad = ctx.createRadialGradient(
      this.x + this.w / 2, this.y + this.h / 2, 0,
      this.x + this.w / 2, this.y + this.h / 2, Math.max(this.w, this.h) / 2
    );
    screenGrad.addColorStop(0, ui.SCOPE_SCREEN_CURVE);
    screenGrad.addColorStop(1, ui.SCOPE_SCREEN);
    ctx.beginPath();
    ctx.fillStyle = screenGrad;
    ctx.roundRect(sx, sy, sw, sh, Math.max(0, r - 1));
    ctx.fill();

    // Top-left glass highlight (glassy reflection)
    ctx.beginPath();
    ctx.strokeStyle = ui.SCOPE_GLASS_HIGHLIGHT;
    ctx.lineWidth = 1;
    ctx.roundRect(sx + 0.5, sy + 0.5, sw - 1, sh - 1, Math.max(0, r - 1.5));
    ctx.stroke();

    // Trace (clipped to screen)
    const data = this.synth.getTimeDomainData();
    if (data) {
      ctx.save();
      ctx.beginPath();
      ctx.roundRect(sx, sy, sw, sh, Math.max(0, r - 1.5));
      ctx.clip();

      const freq = this.synth.getLowestNoteFreq();
      const samplesPerCycle = SAMPLE_RATE / freq;
      const cyclesToDisplay = 2;
      const requiredSamples = Math.round(samplesPerCycle * cyclesToDisplay);

      let triggerIndex = 0;
      for (let i = 0; i < data.length - 1; i++) {
        if (data[i] <= 128 && data[i + 1] > 128) {
          triggerIndex = i;
          break;
        }
      }

      const numSamplesToShow = Math.min(requiredSamples, data.length - triggerIndex);
      const sliceWidth = sw / numSamplesToShow;
      const halfSh = sh / 2;
      const centerY = sy + halfSh;

      // Amber phosphor glow (thick stroke behind)
      ctx.beginPath();
      ctx.strokeStyle = ui.SCOPE_PHOSPHOR_GLOW;
      ctx.lineWidth = 4;
      ctx.lineCap = 'round';
      ctx.lineJoin = 'round';
      let x = sx;
      for (let i = triggerIndex; i < triggerIndex + numSamplesToShow; i++) {
        const v = data[i] / 128;
        const y = centerY + (v - 1) * halfSh;
        if (i === triggerIndex) {
          ctx.moveTo(x, y);
        } else {
          ctx.lineTo(x, y);
        }
        x += sliceWidth;
      }
      ctx.stroke();

      // Bright amber trace
      ctx.beginPath();
      ctx.strokeStyle = ui.SCOPE_PHOSPHOR_AMBER;
      ctx.lineWidth = 1.5;
      x = sx;
      for (let i = triggerIndex; i < triggerIndex + numSamplesToShow; i++) {
        const v = data[i] / 128;
        const y = centerY + (v - 1) * halfSh;
        if (i === triggerIndex) {
          ctx.moveTo(x, y);
        } else {
          ctx.lineTo(x, y);
        }
        x += sliceWidth;
      }
      ctx.stroke();

      // Subtle scanlines (CRT)
      ctx.fillStyle = ui.SCOPE_SCANLINE;
      for (let py = sy; py < sy + sh; py += 3) {
        ctx.fillRect(sx, py, sw, 1);
      }

      ctx.restore();
    }
  }
}

class Sequencer {
  private tempo = 120;
  private triggerPeriod = 1 / 4;
  private noteDuration = 0.5;
  private stepsRecorded: number[][] = [];
  private status: 'recording' | 'stopped' | 'playing' = 'stopped';
  private currentStep = 0;

  constructor(readonly synth: Synth) { }

  setTempo(value: number) {
    this.tempo = value;
    this.synth.sendToWorklet({ command: 'set tempo', value });
  }

  setTriggerPeriod(value: number) {
    this.triggerPeriod = value;
    this.synth.sendToWorklet({ command: 'set trigger period', value });
  }

  setNoteDuration(value: number) {
    this.noteDuration = value;
    this.synth.sendToWorklet({ command: 'set note duration', value });
  }

  start() {
    if (this.stepsRecorded.length === 0) {
      return;
    }

    this.synth.setParam(P_GLIDE, 0);
    this.status = 'playing';
    this.currentStep = 0;
    this.synth.sendToWorklet({ command: 'start sequencer' });
  }

  stop() {
    this.status = 'stopped';
    this.synth.sendToWorklet({ command: 'stop sequencer' });
    for (const pad of pads) {
      pad.isBeingPlayedBySequencer = false;
    }
  }

  onNotePressed(note: number, isTheOnlyOne: boolean) {
    if (this.status !== 'recording') {
      return;
    }

    if (isTheOnlyOne) {
      // this is the first note for this step!
      this.stepsRecorded.push([]);
    }
    this.stepsRecorded.at(-1)!.push(note);
  }

  onMessageFromWorklet(msg: MessageFromWorklet) {
    switch (msg.event) {
      case 'seq step': {
        if (msg.action === 'noteOn') {
          this.currentStep = msg.index;
        }
        for (const note of this.stepsRecorded[msg.index] || []) {
          for (const pad of pads) {
            if (pad.note === note) {
              pad.isBeingPlayedBySequencer = msg.action === 'noteOn';
            }
          }
        }
        break;
      }
      case 'log': {
        showStatus(msg.message);
        break;
      }
      default:
        showStatus('??? ' + JSON.stringify(msg));
        break;
    }
  }

  init() {
    this.synth.sendToWorklet({ command: 'load steps', steps: this.stepsRecorded });
  }

  controls() {
    const options: ThingOptions = { h: () => buttonHeight, isActive: () => activeSynth === this.synth };
    const tempoScrubber = new Scrubber(
      {
        ...options,
        label: (value) => `${value} bpm`,
        minValue: 10,
        maxValue: 200,
        getValue: () => this.tempo,
        setValue: (value) => this.setTempo(value),
      }
    );
    const stepsButton = new Button(
      {
        ...options,
        label: () => this.status === 'playing'
          ? `step ${this.currentStep + 1} / ${this.stepsRecorded.length}`
          : this.stepsRecorded.length === 0
            ? 'record steps'
            : `${this.stepsRecorded.length} steps`,
        pointerDownFn: () => {
          this.stop();
          this.stepsRecorded = [];
          this.status = 'recording';
        },
        pointerUpFn: () => {
          this.synth.sendToWorklet({ command: 'load steps', steps: this.stepsRecorded });
          if (this.stepsRecorded.length > 0) {
            this.start();
          } else {
            this.stop();
          }
        }
      }
    );
    const playButton = new Button(
      {
        ...options,
        label: () => this.status === 'recording' ? 'add rest' : this.status === 'playing' ? 'stop' : 'play',
        pointerDownFn: () => {
          if (this.status === 'recording') {
            this.stepsRecorded.push([]); // add rest
          } else if (this.status === 'playing') {
            this.stop();
          } else {
            this.start();
          }
        },
        pointerUpFn: () => {
          if (this.status === 'recording') {
            this.synth.sendToWorklet({ command: 'load steps', steps: this.stepsRecorded });
            if (this.stepsRecorded.length > 0) {
              this.start();
            } else {
              this.stop();
            }
          }
        }
      }
    );
    return new Column({ w: () => padWidth * 3, isActive: () => activeSynth === this.synth })
      .addChild(tempoScrubber)
      .addChild(stepsButton)
      .addChild(playButton);
  }
}

const seq = new Sequencer(synth2);
const topRow = new Row({ layoutMode: 'row', h: () => buttonHeight * 3 })
  .addChild(synth1.controls())
  .addChild(synth2.controls())
  .addChild(seq.controls());
topLevelThing.options.layoutMode = 'column';
topLevelThing.addChild(topRow);

canvas.addEventListener('pointerdown', (e) => {
  pointerDownForPads(e.pointerId, e.offsetX, e.offsetY, getPressure(e));
  if (fingersById.has(e.pointerId)) {
    canvas.setPointerCapture(e.pointerId);
  }
});

canvas.addEventListener('pointermove', (e) => {
  const finger = fingersById.get(e.pointerId);
  if (finger) {
    finger.move(e.offsetX, e.offsetY, getPressure(e));
    e.preventDefault();
  }
});

canvas.addEventListener('pointerup', onPointerUp);
canvas.addEventListener('pointercancel', onPointerUp);

function onPointerUp(e: PointerEvent) {
  const finger = fingersById.get(e.pointerId);
  if (finger) {
    fingersById.delete(e.pointerId);
    finger.lift();
  }
  canvas.releasePointerCapture(e.pointerId);
}

function render() {
  ctx.fillStyle = ui.BACKGROUND;
  ctx.fillRect(0, 0, innerWidth, innerHeight);

  const padsBottomY = ui.MARGIN + (FIRST_ROW + NUM_ROWS + padRowOffset) * padHeight;
  editor.container.style.top = `${padsBottomY}px`;
  editor.container.style.height = `${ui.EDITOR_HEIGHT_IN_PADS * padHeight}px`;

  for (const pad of pads) {
    pad.render();
  }

  for (const finger of fingersById.values()) {
    finger.render();
  }

  topLevelThing.recursiveRender();
  topLevelThing.recursiveRenderOnTop();

  renderStatus();

  Finger.updateQuantizationOffsets();

  requestAnimationFrame(render);
}

render();

