// ----- interactions -----

function pointerDownForPads(pointerId: number, x: number, y: number, pressure: number) {
  const somePadsAreTouched = [...fingersById.values()].some((f) => !!f.pad);
  for (const pad of pads) {
    if (pad.contains(x, y)) {
      if (activeSynth === seq.synth) {
        seq.onNotePressed(pad.note, !somePadsAreTouched);
      }
      Finger.add(pointerId, x, y, pressure);
      break;
    }
  }
}

function getPressure(e: PointerEvent) {
  if (e.pointerType === 'pen') {
    return e.pressure;
  } else if (e.pointerType === 'touch') {
    return Math.abs(Math.max(e.width, e.height) - 20) / (150 - 20);
  } else {
    return 1;
  }
}

